.footer .container{display:flex;gap:40px;background:hsla(0,0%,100%,.7);border-radius:10px;padding:10px 15px;width:60%;margin:40px auto}.footer .box{flex:1}.footer .box p{font-size:18px;font-weight:600;display:flex;justify-content:center}.footer .box h3{font-size:50px;margin:0 0 20px}.footer .box .my-social{display:flex;flex-direction:column;gap:10px}.footer .box .my-social li{margin-right:10px;display:flex;align-items:center;gap:10px}.footer .box .my-social li .link{color:#090909;display:inline-flex;justify-content:center;align-items:center;width:50px;height:50px;font-size:20px;transition:var(--main-transition);flex:0}.footer .box .line{display:flex;align-items:center;margin-bottom:30px}.footer .box .line .icon{font-size:25px;color:var(--main-color);margin-right:10px}.footer .box .line .info{line-height:1.7;flex:1}.footer .box.form{display:flex;flex-direction:column;align-items:flex-end}.footer .box.form .form-control{width:100%;margin-bottom:20px}.footer .box.form .form-control .input{position:relative;margin:5px 0;width:100%}.footer .box.form .form-control .input svg{position:absolute;top:50%;bottom:50%;transform:translateY(-50%);left:2%;color:var(--main-color)}.footer .box.form .form-control .input input,.footer .box.form .form-control .input textarea{width:100%;padding:5px;border-radius:5px;border-color:var(--main-color);box-shadow:none;border:1px solid var(--main-color);padding:10px 28px;outline:none}.footer .box.form .form-control label{display:flex;align-items:center;gap:2px}.footer .box.form .form-control label.required::after{content:"✲";font-size:10px;color:red}.footer .box.form .form-control .error-message{color:red;font-size:.8rem;font-style:italic}.footer .copyright{text-align:center;padding:25px 0;color:#fff;margin:50px 0 0;border-top:1px solid #444}@media(max-width: 767px){.footer .container{flex-direction:column;gap:5px;width:80%}}